@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 400;
  src: url(/static/media/Inter-Regular.dc131113.woff2) format("woff2"),
       url(/static/media/Inter-Regular.3ac83020.woff) format("woff");
}

@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 500;
  font-display: swap;
  src: url(/static/media/Inter-Medium.75db5319.woff2) format("woff2"),
       url(/static/media/Inter-Medium.c0638bea.woff) format("woff");
}


@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 900;
  src: url(/static/media/Inter-Black.661569af.woff2) format("woff2"),
       url(/static/media/Inter-Black.d0b121f3.woff) format("woff");
}

.page {
  font-family: "Inter", Arial, sans-serif;
  background-color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
   /* text-rendering: optimizeLegibility; */

}

.page__container {
  min-width: 320px;
  max-width: 880px;
  margin: 0 auto;
}

@media screen and (min-width: 1280px) {
  .page__container {
    padding: 0 200px 0 200px;
  }
}

.header {
  color: #ffffff;
  display: grid;
  justify-content: space-between;
  align-items: center;
  grid-template-areas:
    'auth auth'
    'logo burger';
  padding: 28px 33px 31px 27px;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  line-height: 0;
}

@media screen and (min-width: 768px) {
  .header {
    grid-template-areas: 'logo  burger';
  }
}

@media screen and (min-width: 1280px) {
  .header {
    padding: 45px 0 40px 0;
  }
}

.header__logo-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  grid-area: logo;
  width: 103px;
  height: 24px;
}

@media screen and (min-width: 1024px) {
  .header__logo {
    width: 142px;
    height: 33px;
  }
}

.header__burger {
  grid-area: burger;
  display: flex;
  flex-direction: column;
  width: 24px;
  grid-row-gap: 8px;
  row-gap: 8px;
}

.header__burger:hover {
  cursor: pointer;
}

@media screen and (min-width: 768px) {
  .header__burger {
    display: none;
  }
}

.header__burger_active {
  /* transform: rotate(90deg); */
}

.header__burger-item {
  height: 3px;
  width: 100%;
  background-color: #fff;
}

.header__link {
  grid-area: burger;
  color: #fff;
  text-decoration: none;
}

.header__auth-container {
  grid-area: auth;
  display: none;
}

@media screen and (min-width: 768px) {
  .header__auth-container {
    grid-area: burger;
    display: flex;
    flex-direction: row;
    grid-column-gap: 24px;
    -webkit-column-gap: 24px;
            column-gap: 24px;
    padding: 0;
  }
}

.header__auth-container_active {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-row-gap: 18px;
  row-gap: 18px;
  padding: 7px 0 40px 0;
}

.header__auth-email {
  margin: 0;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
}

.header__auth-link {
  margin: 0;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #a9a9a9;
  cursor: pointer;
}

.main {
}
.profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 42px 19px 0 19px;
}

@media screen and (min-width: 646px) {
  .profile {
    flex-direction: row;
  }
}

@media screen and (min-width: 1280px) {
  .profile {
    padding: 40px 0px 0px 0px;
  }
}

.profile__avatar-wrap {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  position: relative;
}

.profile__avatar-wrap:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: url(/static/media/profile__avatar-edit-icon.d476a8b8.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-color: rgba(0, 0, 0, 0.8);
  cursor: pointer;
  visibility: hidden;
}
.profile__avatar-wrap:hover:after {
  visibility: visible;
}

.profile__avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
}

.profile__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 26px 0 0 0;
  color: #ffffff;
}

@media screen and (min-width: 646px) {
  .profile__info {
    align-items: flex-start;
    margin: 0 0 0 30px;
    padding: 0;
  }
}

.profile__name {
  display: flex;
  align-items: center;
  position: relative;
}

.profile__name-text {
  max-width: 200px;
  margin: 0;
  font-weight: 500;
  font-size: 27px;
  line-height: 33px;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (min-width: 1024px) {
  .profile__name-text {
    max-width: 300px;
    font-size: 42px;
    line-height: 48px;
  }
}

.profile__button-edit {
  background-image: url(/static/media/profile__button-edit.ac9f2937.svg);
  background-repeat: no-repeat;
  background-color: transparent;
  width: 24px;
  height: 24px;
  background-size: 24px;
  margin: 0 0 0 18px;
  padding: 0;
  border: 0;
}

.profile__button-edit:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .profile__button-edit {
    position: absolute;
    right: -28px;
    width: 18px;
    height: 18px;
    background-size: 18px;
    margin: 0;
  }
}

.profile__job {
  max-width: 200px;
  margin: 8px 0 0 0;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (min-width: 1024px) {
  .profile__job {
    max-width: 300px;
    margin: 8px 0 0 0;
    font-size: 18px;
    line-height: 22px;
  }
}

.profile__button-plus {
  width: 282px;
  height: 50px;
  padding: 0;
  margin: 32px 0 0 0;
  border: 2px solid #ffffff;
  border-radius: 2px;
  background-color: #000;
  background-image: url(/static/media/profile__button-plus.e0270e67.svg);
  background-position: center;
  background-repeat: no-repeat;
  box-sizing: border-box;
}

.profile__button-plus:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media screen and (min-width: 646px) {
  .profile__button-plus {
    width: 150px;
    height: 50px;
    margin: 0 0 0 auto;
  }
}

.elements {
  display: grid;
  grid-template: -webkit-max-content / 282px;
  grid-template: max-content / 282px;
  justify-content: center;
  grid-gap: 20px;
  gap: 20px;
  padding: 36px 19px 0 19px;
}

@media screen and (min-width: 619px) {
  .elements {
    grid-template: -webkit-max-content / repeat(2, 282px);
    grid-template: max-content / repeat(2, 282px);
    grid-gap: 20px 17px;
    gap: 20px 17px;
  }
}

@media screen and (min-width: 918px) {
  .elements {
    grid-template: -webkit-max-content / repeat(3, 282px);
    grid-template: max-content / repeat(3, 282px);
  }
}

@media screen and (min-width: 1280px) {
  .elements {
    padding: 51px 0 0 0;
  }
}

.element {
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  overflow: hidden;
  font-size: 0;
  position: relative;
}

.element__image {
  width: 282px;
  height: 282px;
  object-fit: cover;
}

.element__caption-area {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #ffffff;
  padding: 25px 20px 25px 21px;
}

.element__trash {
  width: 18px;
  height: 19px;
  background-image: url(/static/media/elements-trash.4831282e.svg);
  position: absolute;
  top: 20px;
  right: 20px;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: transparent;
  border: 0;
  padding: 0;
}
.element__trash:hover {
  cursor: pointer;
  opacity: 0.6;
}

.element__trash_hidden {
  display: none;
}
.element__name {
  max-width: 190px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  margin: 0;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  color: #000000;
}

.element__button-like {
  width: 22px;
  height: 19px;
  background-image: url(/static/media/elements-like.00b1c22a.svg);
  background-size: cover;
  background-repeat: no-repeat;
  background-color: transparent;
  padding: 0;
  border: 0;
}

.element__button-like:hover {
  opacity: 0.5;
  cursor: pointer;
}

.element__button-like_active {
  background-image: url(/static/media/elements-like-active.3d813f81.svg);
}

.element__like-counter {
  display: block;
  font-size: 13px;
  line-height: 16px;
  text-align: center;
  color: #000000;
  margin: 3px 0 0 0;
}

.popup {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s, opacity 0.3s;
}

.popup_name_img {
  background-color: rgba(0, 0, 0, 0.9);
}

.popup_name_auth {
  background-color: transparent;
  position: static;
  margin: 40px 0 0 0;
  opacity: 1;
  visibility: visible;
}

.popup__container {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  width: 282px;
  border-radius: 10px;
  padding: 25px 22px;
  box-sizing: border-box;
}

@media screen and (min-width: 1024px) {
  .popup__container {
    width: 430px;
    padding: 34px 36px;
  }
}

.popup__container_name_auth {
  background-color: transparent;
}

.popup__close-icon {
  width: 20px;
  height: 20px;
  background-image: url(/static/media/profile-edit-close.471a937b.svg);
  background-size: cover;
  background-color: transparent;
  position: absolute;
  top: -36px;
  right: 0;
  border: 0;
}
.popup__close-icon:hover {
  cursor: pointer;
  opacity: 0.6;
}
@media screen and (min-width: 1024px) {
  .popup__close-icon {
    width: 32px;
    height: 32px;
    right: -41px;
    top: -39px;
  }
}

.popup__close-icon_name_auth {
  position: static;
}

@media screen and (min-width: 768px) {
  .popup__close-icon_name_auth {
    display: none;
  }
}

.popup__title {
  font-weight: 900;
  font-size: 18px;
  line-height: 22px;
  color: #000;
  margin: 0;
}
@media screen and (min-width: 1024px) {
  .popup__title {
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
  }
}

.popup__title_name_auth {
  text-align: center;
  color: #fff;
}

.popup__form {
  display: flex;
  flex-direction: column;
  padding: 75px 0 0 0;
}

@media screen and (min-width: 1024px) {
  .popup__form {
    padding: 48px 0 0 0;
  }
}

.popup__form_name_auth {
  padding: 40px 0 0 0;
}

@media screen and (min-width: 1024px) {
  .popup__form_name_auth {
    padding: 50px 0 0 0;
  }
}

.popup__input {
  border: 0;
  width: 100%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  font-size: 14px;
  line-height: 17px;
  padding: 0 0 10px 0;
  box-sizing: border-box;
}

.popup__input:focus {
  outline: 0;
}

.popup__button {
  min-height: 46px;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #fff;
  background-color: #000;
  border: none;
  border-radius: 2px;
  margin: 15px 0 0 0;
  padding: 0;
  box-sizing: border-box;
}

.popup__button:hover {
  cursor: pointer;
  opacity: 0.8;
}

@media screen and (min-width: 1024px) {
  .popup__button {
    min-height: 50px;
    font-size: 18px;
    line-height: 22px;
    margin: 18px 0 0 0;
  }
}

.popup__button_name_auth {
  color: #000;
  background-color: #fff;
  margin: 173px 0 0 0;
}

@media screen and (min-width: 1024px) {
  .popup__button_name_auth {
    margin: 216px 0 0 0;
  }
}

.popup__button_disabled {
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  pointer-events: none;
  background-color: #ffffff;
  color: #000000;
  border: 1px solid #000000;
  opacity: 0.2;
}

.popup__img-container {
  display: flex;
  position: relative;
  box-sizing: border-box;
  flex-direction: column;
  background-color: transparent;
  padding: 0;
  margin: 0;
}

.popup__img {
  max-width: 75vw;
  max-height: 75vh;
  object-fit: cover;
  box-sizing: border-box;
}

.popup__img-label {
  font-size: 12px;
  line-height: 15px;
  color: #ffffff;
  margin: 10px 0 0 0;
}

.popup__error {
  min-height: 26px;
  display: block;
  visibility: hidden;
  font-size: 12px;
  line-height: 12px;
  padding: 2px 0 0 0;
  color: #ff0000;
}

/* parametr */
.popup__error_visible {
  visibility: visible;
}

/* parametr */
.popup__input_type_error {
  border-bottom: 1px solid #ff0000;
}

.popup__input_name_auth {
  background-color: transparent;
  border-bottom: 2px solid #cccccc;
  color: #fff;
  margin: 30px 0 0 0;
}

.popup__input_name_auth:first-child {
  margin: 0;
}

.popup__text-container {
  display: flex;
  align-items: baseline;
  margin: 20px 0 0 0;
  align-self: center;
}

.popup__text {
  color: #fff;
  margin: 0;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
}

.popup__text-link {
  cursor: pointer;
  margin: 0 0 0 5px;
  color: #fff;
  text-decoration: none;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
}

.popup__auth-img {
  width: 120px;
  height: 120px;
  align-self: center;
  margin: 25px 0 0 0;
}

.popup__auth-label {
  align-self: center;
  margin: 40px 0 0 0;
  width: 244px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 900;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  color: #000000;
}

.popup_opened {
  opacity: 1;
  visibility: visible;
  position: absolute;
}

.footer {
  max-width: 880px;
  margin: 0 auto;
  padding: 48px 19px 36px 19px;
}

@media screen and (min-width: 1024px) {
  .footer {
    padding: 66px 0 36px 0;
  }
}

.footer__text {
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #545454;
  margin: 0;
}

@media screen and (min-width: 1024px) {
  .footer__text {
    font-size: 18px;
    line-height: 22px;
  }
}



